import React, { useState } from "react"
import { Link } from "gatsby"
import Image from '../components/image';
import Layout from "../components/layout"
import SEO from "../components/seo";
import { AppBar, Dialog, IconButton, Typography, Button, Toolbar } from "@material-ui/core";
import { Close as CloseIcon } from "@material-ui/icons";
import Graph from '../components/charactersGraph';

import wikiData from '../data/wiki.json';
import '../styles/wiki.scss';
import charsGraphImg from '../images/charsGraph.png';

const description = `Which stranger things character could you be? Take the personality quiz/test and find your associated stranger things character with a large variety of characters. It's not another boring quiz!`;
const keywords = [
  "stranger things stuffs",
  "stranger things",
  "nerdy quiz",
  "nerdy test",
  "character quiz",
  "ST",
  "Eleven",
  "Mike",
  "Will",
  "Dustin",
  "Lucas",
  "Mad Max",
  "Dr Brenner",
  "Nancy",
  "Jonathan",
  "Suzie",
  "Erica",
  "Billy",
  "Dr Owen",
  "Murray",
  "Joyce",
  "Bob Newby",
  "Jim Hopper",
  "Karen",
  "Mayor Larry",
  "Alexei",
  "Robin",
  "Steve",
  "Scott",
  "Ted"
]

const Wiki = () => {
  const [open, setOpen] = useState(false);
  return (
    <Layout navNumber={4}>
      <SEO
        title="Learn More About Stranger Things Characters (Cast) - Wiki"
        description={description}
        keywords={keywords.join(",")}
      />
      <div id="Wiki">
        <div className="head">
          <Image className="chars" name="wikichars" />
          <h1>Learn much more about the show characters in stranger-things-<b>wiki</b></h1>
        </div>
        <p>
          According to stranger things <a href="https://strangerthings.fandom.com/" target="_blank" rel="noopener noreferrer">fandom</a>:
        </p>
        <blockquote>
          Stranger Things is a television-format series created by The Duffer Brothers. It was released as a Netflix original series on July 15, 2016.
          The show takes place in the fictional town of Hawkins, Indiana in the early-to-mid 1980s.
          The first season revolves around the disappearance of Will Byers, while the second season explores the repercussions of the mysterious events of season 1.
          The third season focuses on "forces of evil that are new."
          The unreleased fourth season will focus on a story outside of Hawkins.
        </blockquote>
        <div>
          <h2>Click on the image below to use the floating graph to find characters and relations:</h2>
          <div className="graph-click" onClick={() => setOpen(true)}>
            <img src={charsGraphImg} alt="characters graph" />
          </div>
        </div>
        <br />
        <Link to="/">Go back to the homepage</Link>
      </div>
      <Dialog fullScreen open={open} onClose={() => setOpen(false)}>
        <AppBar className="app-bar">
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={() => setOpen(false)} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className="title">
              Characters
            </Typography>
          </Toolbar>
        </AppBar>
        <Graph />
      </Dialog>
    </Layout>
  );
}

export default Wiki
