const config = {
  nodeHighlightBehavior: true,
  linkHighlightBehavior: false,
  automaticRearrangeAfterDropNode: true,
  panAndZoom: false,
  directed: false,
  staticGraph: false,
  staticGraphWithDragAndDrop: false,

  focusZoom: 1,
  minZoom: 0.5,
  maxZoom: 1.25,
  width: (typeof window !== "undefined") ? window.innerWidth : 800,
  height: 600,
  focusAnimationDuration: 0.2,
  node: {
    color: "#C11B1F",
    size: 1000,
    highlightStrokeColor: "#2b2b2b",
    symbolType: "wye",
    fontSize: 20,
    highlightFontSize: 22,
    labelPosition: "top",
    renderLabel: false,
    viewGenerator: (node) => {
      if (node.symbolType === "circle") {
        return (
          <div className="category-node">{node.id}</div>
        )
      }
      return (
        <div>
          <Tooltip placement="left" title={node.name}>
            <Avatar className="avatar-node">
              <Image name={node.id} />
            </Avatar>
          </Tooltip>
        </div>
      )
    }
  },
  link: {
    highlightColor: "lightblue",
    type: "CURVE_SMOOTH"
  },
  d3: {
    disableLinkForce: false,
    gravity: -550,
    linkStrength: 3,
  }
};


export default function CharactersGraph() {
  return (
    <div>
      <Graph
        ref={gRef}
        id="Chars"
        data={data}
        config={config}
      />
      <div className="graph-overlay" onClick={(e) => (e.target.style = "display: none")}>
        Click To Use
      </div>
    </div>
  )
}
